//Codes-Export
export const ExportCodes = async (Query = "") => {
  const Token = GetToken();
  if (!Token) return false;

  const Response = await fetch(`${Host}/Admin/Codes-Export`, {
    method: "POST",
    body: JSON.stringify({
      Token: Token,
      Query: Query,
    }),
    headers: {
      "content-type": "application/json",
    },
  });

  if (
    Response.status === 401 ||
    Response.status === 404 ||
    Response.status === 500
  )
    return { Valid: false };

  const Data = await Response.json();

  //Download File
  const FileName = `${new Date()}.${Data?.ext ? Data?.ext : "txt"}`;
  const FileBlob = new Blob([Data?.file], {
    type:
      Data?.ext == "txt"
        ? "text/plain"
        : Data?.ext == "json"
        ? "application/json"
        : "application/csv",
  });
  //New file from FileBlob
  const FileDownloadable = new File([FileBlob], FileName, {
    type:
      Data?.ext == "txt"
        ? "text/plain"
        : Data?.ext == "json"
        ? "application/json"
        : "application/csv",
  });

  const URL = window.URL.createObjectURL(FileDownloadable);
  const Link = document.createElement("a");
  Link.href = URL;
  Link.setAttribute("download", FileName);
  document.body.appendChild(Link);
  Link.style.display = "none";
  Link.click();
  document.body.removeChild(Link);
  window.URL.revokeObjectURL(URL);

  return Data;
};

