//New react functional component
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import {
  CardActionArea,
  Typography,
  TableContainer,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Paper,
  Table,
  TablePagination,
} from "@mui/material";
import {
  GetSeriesEpisodesAndCount,
  GetSeriesEpisodesAndCountSearch,
  GetSeriesFragmentEpisodesAndCountSearch,
  GetSettings,
} from "../Assets/HttpBank";
import { Delete, Edit } from "@mui/icons-material";
import CategoriesItem from "./CategoriesItem";
import SeriesItem from "./SeriesItem";
import SeriesEpisodes from "../Pages/SeriesEpisodes";
import SeriesEpisodeItem from "./SeriesEpisodeItem";

function createData(name, index) {
  return { name, index };
}

const EpisodesTable = (prop) => {
  const {
    RefreshComp,
    onEdit,
    Search,
    Series: SeriesID,
    canDelete,
    canEdit,
    onDeletedRefresh,
  } = prop;

  const navigate = useNavigate();

  const [Page, setPage] = useState(0);
  const [Skip, setSkip] = useState(0);

  const [Settings, setSettings] = useState({});

  const [NumberOfRows, setNumberOfRows] = useState(0);
  useEffect(() => {
    (async () => {
      const Data = await GetSettings();
      const { Settings, Valid } = Data ? Data : {};
      setSettings(Settings ? Settings : {});
      setNumberOfRows(Settings ? Settings.NumberOfRows : 6);
    })();
  }, []);

  const [Episodes, setEpisodes] = useState([]);
  const [Count, setCount] = useState(0);

  const [RefreshCompInside, setRefreshCompInside] = useState(false);

  const RefreshSeries = async () => {
    if (NumberOfRows >= 1) {
      if (SeriesID) {
        let Data = await GetSeriesFragmentEpisodesAndCountSearch(
          Skip,
          NumberOfRows,
          Search,
          SeriesID
        );

        if (!Data) {
          return RefreshSeries();
        }

        const { Count, Episodes = [], Valid } = Data ? Data : {};
        console.log(Episodes);
        if (!Valid) return;
        setEpisodes(Episodes);
        setCount(Count);
      } else {
        const Data = await GetSeriesEpisodesAndCount(Skip, NumberOfRows);
        const { Count, Episodes, Valid } = Data ? Data : {};
        if (!Valid) return;
        setEpisodes(Episodes);
        setCount(Count);
      }
    }
  };

  useEffect(() => {
    if (Search?.length <= 0) {
      RefreshSeries();
    }
  }, [Skip, NumberOfRows, RefreshComp, RefreshCompInside, Search]);

  useEffect(() => {
    (async () => {
      if (NumberOfRows >= 1) {
        if (SeriesID) {
          let Data = await GetSeriesFragmentEpisodesAndCountSearch(
            Skip,
            NumberOfRows,
            Search,
            SeriesID
          );

          if (!Data) {
            return RefreshSeries();
          }

          const { Count, Episodes = [], Valid } = Data ? Data : {};
          if (!Valid) return;
          setEpisodes(Episodes);
          setCount(Count);
        } else {
          let Data = await GetSeriesEpisodesAndCountSearch(
            Skip,
            NumberOfRows,
            Search
          );

          if (!Data) {
            return RefreshSeries();
          }

          const { Count, Episodes = [], Valid } = Data ? Data : {};
          if (!Valid) return;
          setEpisodes(Episodes);
          setCount(Count);
        }
      }
    })();
  }, [Skip, NumberOfRows, RefreshComp, RefreshCompInside, Search, SeriesID]);

  return (
    <TableContainer component={Paper} style={{ maxHeight: "100%" }}>
      <Table sx={{ minWidth: 650 }} aria-label="caption table">
        <TableHead>
          <TableRow>
            <TableCell>Name</TableCell>
            <TableCell>Views</TableCell>
            <TableCell>Action</TableCell>
          </TableRow>
        </TableHead>
        <TableBody
          style={{
            opacity: Episodes.length <= 0 ? 0 : 1,
            transition: "all .6s",
          }}
        >
          {Episodes.map((row, index) => {
            return (
              <SeriesEpisodeItem
                canDelete={canDelete}
                canEdit={canEdit}
                SeriesID={SeriesID}
                key={index}
                row={row}
                onDeletedRefresh={onDeletedRefresh}
                RefreshCompInside={() => {
                  setRefreshCompInside(!RefreshCompInside);
                }}
                onEdit={() => {
                  onEdit?.(row);
                }}
              />
            );
          })}
        </TableBody>
      </Table>

      <div
        style={{
          width: "100%",
          display: "flex",

          alignItems: "center",
          justifyContent: "center",

          padding: 10,
        }}
      >
        <TablePagination
          component="div"
          count={Count}
          page={Page}
          onPageChange={(c, page) => {
            setPage(page);
            setSkip(page * NumberOfRows);
            //   alert(page);
          }}
          rowsPerPage={NumberOfRows}
          rowsPerPageOptions={[6]}
        />
      </div>
    </TableContainer>
  );
};

export default EpisodesTable;
