//New react functional component
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

import {
  Card,
  Input,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Button,
  Checkbox,
  Snackbar,
  Alert,
  CardActionArea,
} from "@mui/material";

import { Cancel } from "@mui/icons-material";

import { CreateEpisode } from "../Assets/HttpBank";

const AddEpisode = (prop) => {
  const { Series: SeriesID, EpisodeName, Url } = prop;
  const navigate = useNavigate();
  const [Name, setName] = useState(EpisodeName ? EpisodeName : "");
  const [EpisodeUrl, setEpisodeUrl] = useState(Url);
  const [SubCategories, setSubCategories] = useState([]);
  const [Featured, setFeatured] = useState(false);
  const [Series, setSeries] = useState(SeriesID);

  const Create = async () => {
    prop?.Message?.({ Message: "Creating Episode...", Type: "info" });
    const CreatedSeries = await CreateEpisode({
      Name,
      Url: EpisodeUrl,
      Series: SeriesID,
    });

    if (!CreatedSeries?.Valid) {
      prop?.Message?.({
        Message: "Couldn't create the Episode.",
        Type: "error",
      });
      return;
    }

    prop?.Message?.({
      Message: `Episode created!`,
      Type: "success",
    });

    prop?.Refresh?.();
    prop?.Close?.();

    //Show snackbar with the message
  };

  return (
    <Card
      style={{
        position: "absolute",
        display: "flex",
        alignItems: "flex-start",
        justifyContent: "flex-start",
        flexDirection: "column",
        width: "30%",
        // height: "70%",
        zIndex: 2,
        padding: 20,
      }}
    >
      <div
        style={{
          width: "100%",
          display: "flex",
          flexDirection: "row",
          alignItems: "center",
          justifyContent: "space-between",
        }}
      >
        <h2 style={{ fontSize: 18, color: "grey", fontWeight: "bolder" }}>
          Add Episode.
        </h2>

        <CardActionArea
          onClick={() => {
            prop?.Close?.();
          }}
          style={{
            borderRadius: 20,
            width: 30,
            height: 30,
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <Cancel style={{ width: 20, height: 20, color: "black" }} />
        </CardActionArea>
      </div>
      <div style={{ width: "95%", height: 1, backgroundColor: "lightgrey" }} />

      <div
        style={{
          width: "100%",
          display: "flex",
          flexDirection: "row",
          alignItems: "center",
          justifyContent: "space-between",
          marginLeft: 10,
          marginTop: 30,
        }}
      >
        <Input
          type={"text"}
          value={Name}
          style={{
            width: "90%",
            height: 50,
            backgroundColor: "#e6e6e6",
            padding: 10,
            borderRadius: 10,
          }}
          disableUnderline={true}
          placeholder={"Episode name"}
          onChange={(e) => {
            setName(e.target.value);
          }}
        />
      </div>

      <Input
        style={{
          width: "90%",
          height: 50,
          backgroundColor: "#e6e6e6",
          padding: 10,
          marginLeft: 10,
          marginTop: 30,
          borderRadius: 10,
        }}
        disableUnderline={true}
        value={EpisodeUrl}
        placeholder={"Episode Link"}
        onChange={(e) => {
          setEpisodeUrl(e.target.value);
        }}
      />

      <FormControl
        style={{
          width: "90%",
          height: 60,
          //   backgroundColor: "lightgrey",
          padding: 10,
          marginTop: 30,
          borderRadius: 10,
        }}
      >
        <InputLabel id="demo-simple-select-label">Series ID?</InputLabel>
        <Select
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          value={SeriesID}
          disabled={true}
          label="Age"
          // onChange={handleChange}
        >
          <MenuItem value={SeriesID}>{`Series ID: ${SeriesID}`}</MenuItem>
        </Select>
      </FormControl>

      <div style={{ width: "95%", height: 1, backgroundColor: "lightgrey" }} />

      <Button
        style={{
          width: "90%",
          marginLeft: 10,
          marginTop: 30,
          textAlign: "start",
          alignItems: "center",
          justifyContent: "flex-start",
          borderRadius: 10,
        }}
        onClick={() => {
          Create();
        }}
      >
        <h5>Create</h5>
      </Button>
    </Card>
  );
};

export default AddEpisode;
