//New react functional component
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import {
  CardActionArea,
  Typography,
  TableContainer,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Paper,
  Table,
  TablePagination,
} from "@mui/material";
import { DeleteEpisode, GetSeriesByID } from "../Assets/HttpBank";
import { Delete, Edit, FolderOpen } from "@mui/icons-material";
import { Host } from "../Assets/Receiver";

function createData(name, index) {
  return { name, index };
}

const SeriesEpisodeItem = (prop) => {
  const {
    row,
    RefreshCompInside,
    onEdit,
    SeriesID,
    canDelete,
    canEdit,
    onDeletedRefresh,
  } = prop;

  // const [Logo, setLogo] = useState(
  //   "https://cdnb.artstation.com/p/assets/images/images/023/674/391/large/evgeniy-bogdanov-defaault.jpg?1579980223"
  // );

  return (
    <TableRow>
      <TableCell style={{ minWidth: 120 }}>{row.Name}</TableCell>
      <TableCell style={{ minWidth: 120 }}>
        <div
          onClick={() => {
            window.location = `/LiveViews/${row?._id}/Episodes/false`;
          }}
          style={{
            fontSize: 20,
            fontWeight: "bolder",
            cursor: "pointer",
          }}
        >
          {row?.Views}
        </div>
      </TableCell>
      <TableCell style={{ minWidth: 120 }}>
        <div style={{ display: "flex", flexDirection: "row" }}>
          {canEdit && (
            <CardActionArea
              onClick={() => {
                onEdit?.();
              }}
              style={{
                width: 30,
                height: 30,
                borderRadius: 10,
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <Edit style={{ width: 20, height: 20, color: "primary" }} />
            </CardActionArea>
          )}

          {canDelete && (
            <CardActionArea
              onClick={async () => {
                const CategoryDeleteAction = await DeleteEpisode({
                  EpisodeID: row._id,
                });

                if (CategoryDeleteAction.Code === 403) {
                  return alert("Can't delete this Series, for some reason.");
                }
                RefreshCompInside?.();
                onDeletedRefresh?.();
              }}
              style={{
                width: 30,
                height: 30,
                borderRadius: 10,
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <Delete style={{ width: 20, height: 20, color: "primary" }} />
            </CardActionArea>
          )}
        </div>
      </TableCell>
    </TableRow>
  );
};

export default SeriesEpisodeItem;
