import { useEffect, useState } from "react";
import { Navigate, useNavigate } from "react-router-dom";
import {
  CardActionArea,
  Typography,
  TableContainer,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Paper,
  Table,
  TablePagination,
} from "@mui/material";
import { DeleteMovies, DeleteSeries } from "../Assets/HttpBank";
import { Delete, Edit, FolderOpen } from "@mui/icons-material";
import { Host } from "../Assets/Receiver";

function createData(name, index) {
  return { name, index };
}

const MoviesItem = (prop) => {
  const { row, RefreshCompInside, onEdit, canDelete, canEdit } = prop;
  const navigate = useNavigate();

  // const [Logo, setLogo] = useState(
  //   "https://cdnb.artstation.com/p/assets/images/images/023/674/391/large/evgeniy-bogdanov-defaault.jpg?1579980223"
  // );

  // useEffect(() => {
  //   (async () => {
  //     const Image = await ProxyImage(row.Logo);
  //     if (Image) {
  //       console.log(Image);
  //       setLogo(Image);
  //     }
  //   })();
  // }, []);

  return (
    <TableRow>
      <TableCell style={{ minWidth: 120 }}>
        <img
          alt={""}
          src={
            row?.Picture?.includes?.("http")
              ? `${Host}/Admin/ProxyImage?ImageURL=${row.Picture}`
              : row.Picture
          }
          width={110}
          height={110}
        />
      </TableCell>
      <TableCell style={{ minWidth: 120 }}>{row.Name}</TableCell>
      <TableCell style={{ minWidth: 120 }}>
        <div
          onClick={() => {
            window.location = `/LiveViews/${row?._id}/Movies/false`;
          }}
          style={{
            fontSize: 20,
            fontWeight: "bolder",
            cursor: "pointer",
          }}
        >
          {row?.Views}
        </div>
      </TableCell>
      <TableCell style={{ minWidth: 120 }}>
        <div style={{ display: "flex", flexDirection: "row" }}>
          {canEdit && (
            <CardActionArea
              onClick={() => {
                onEdit?.();
              }}
              style={{
                width: 30,
                height: 30,
                borderRadius: 10,
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <Edit style={{ width: 20, height: 20, color: "primary" }} />
            </CardActionArea>
          )}

          {canDelete && (
            <CardActionArea
              onClick={async () => {
                const CategoryDeleteAction = await DeleteMovies({
                  MoviesID: row._id,
                });

                if (CategoryDeleteAction.Code === 403) {
                  return alert("Can't delete this Movie, for some reason.");
                }
                RefreshCompInside?.();
              }}
              style={{
                width: 30,
                height: 30,
                borderRadius: 10,
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <Delete style={{ width: 20, height: 20, color: "primary" }} />
            </CardActionArea>
          )}
        </div>
      </TableCell>
    </TableRow>
  );
};

export default MoviesItem;
