//New react functional component
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import {
  CardActionArea,
  Typography,
  TableContainer,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Paper,
  Table,
  TablePagination,
} from "@mui/material";
import {
  GetLivesAndCount,
  GetLivesAndCountSearch,
  GetMoviesAndCount,
  GetMoviesAndCountSearch,
  GetSeriesAndCount,
  GetSeriesAndCountSearch,
  GetSettings,
} from "../Assets/HttpBank";
import { Delete, Edit } from "@mui/icons-material";
import CategoriesItem from "./CategoriesItem";
import SeriesItem from "./SeriesItem";
import MoviesItem from "./MoviesItem";
import LivesItem from "./LivesItem";

import { Code, Host, MacAddress, SerialNumber } from "../Assets/Receiver";

import socketio from "socket.io-client";

function createData(name, index) {
  return { name, index };
}

const LivesTable = (prop) => {
  const {
    RefreshComp,
    onEdit,
    canStatus,
    Search,
    canDelete,
    canEdit,
    onCounts,
  } = prop;

  const navigate = useNavigate();

  const [Page, setPage] = useState(0);
  const [Skip, setSkip] = useState(0);

  const [Settings, setSettings] = useState({});

  const [NumberOfRows, setNumberOfRows] = useState(0);
  useEffect(() => {
    (async () => {
      const Data = await GetSettings();
      const { Settings, Valid } = Data ? Data : {};
      setSettings(Settings ? Settings : {});
      setNumberOfRows(Settings ? Settings.NumberOfRows : 6);
    })();
  }, []);

  const [Lives, setLives] = useState([]);
  const [Count, setCount] = useState(0);

  const [RefreshCompInside, setRefreshCompInside] = useState(false);

  const RefreshSeries = async () => {
    if (NumberOfRows >= 1) {
      const Data = await GetLivesAndCount(Skip, NumberOfRows);
      const { Count, Lives, OfflineCount, OnlineCount, Valid } = Data
        ? Data
        : {};
      if (!Valid) return;

      onCounts?.(OfflineCount, OnlineCount);
      setLives(Lives);
      setCount(Count);
    }
  };

  useEffect(() => {
    if (Search?.length <= 0) {
      RefreshSeries();
    }
  }, [Skip, NumberOfRows, RefreshComp, RefreshCompInside, Search]);

  useEffect(() => {
    (async () => {
      if (NumberOfRows >= 1) {
        const Data = await GetLivesAndCountSearch(Skip, NumberOfRows, Search);
        if (!Data) {
          return RefreshSeries();
        }

        const { Count, Lives, OfflineCount, OnlineCount, Valid } = Data
          ? Data
          : {};
        if (!Valid) return;

        onCounts?.(OfflineCount, OnlineCount);
        setLives(Lives);
        setCount(Count);
      }
    })();
  }, [Skip, NumberOfRows, RefreshComp, RefreshCompInside, Search]);

  useEffect(() => {
    const socket = socketio(
      `http://realtime.${Host.replace("http://", "").replace("https://", "")}`
    ).connect();

    Lives.forEach((Live) => {
      socket.on(`${Live?._id}`, ({ Status, ID }) => {
        setTimeout(() => {
          console.log(Status, ID);
          let Update = false;
          let AppliedLives = [...Lives];

          AppliedLives = AppliedLives.filter((Live) => {
            if (`${Live?._id}` === `${ID}`) {
              Update = true;
              Live.Status = Status;
            }
            return Live;
          });

          if (Update) {
            setLives([...AppliedLives]);
          }
        }, 1);
      });
    });

    Lives.forEach((Live) => {
      socket.on(`Views_${Live?._id}`, ({ LiveViews, ID, Name }) => {
        setTimeout(() => {
          console.log(LiveViews, ID, Name);
          let Update = false;
          let AppliedLives = [...Lives];

          AppliedLives = AppliedLives.filter((Live) => {
            if (`${Live?._id}` === `${ID}`) {
              Update = true;
              Live.LiveViews = LiveViews;
            }
            return Live;
          });

          if (Update) {
            setLives([...AppliedLives]);
          }
        }, 2);
      });
    });
    return () => {
      socket.off();
      socket.offAny();
      socket.close();
    };
  }, [Lives]);

  return (
    <TableContainer component={Paper} style={{ maxHeight: "100%" }}>
      <Table sx={{ minWidth: 650 }} aria-label="caption table">
        <TableHead>
          <TableRow>
            <TableCell>Picture</TableCell>
            <TableCell>Name</TableCell>
            <TableCell>Views</TableCell>
            <TableCell>Live Views</TableCell>
            <TableCell>Action</TableCell>
          </TableRow>
        </TableHead>
        <TableBody
          style={{ opacity: Lives.length <= 0 ? 0 : 1, transition: "all .6s" }}
        >
          {Lives.map((row, index) => {
            return (
              <LivesItem
                canDelete={canDelete}
                canEdit={canEdit}
                canStatus={canStatus}
                key={index}
                row={row}
                RefreshCompInside={() => {
                  setRefreshCompInside(!RefreshCompInside);
                }}
                onEdit={() => {
                  onEdit?.(row);
                }}
              />
            );
          })}
        </TableBody>
      </Table>

      <div
        style={{
          width: "100%",
          display: "flex",

          alignItems: "center",
          justifyContent: "center",

          padding: 10,
        }}
      >
        <TablePagination
          component="div"
          count={Count}
          page={Page}
          onPageChange={(c, page) => {
            setPage(page);
            setSkip(page * NumberOfRows);
            //   alert(page);
          }}
          rowsPerPage={NumberOfRows}
          rowsPerPageOptions={[6]}
        />
      </div>
    </TableContainer>
  );
};

export default LivesTable;
