//New react functional component
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

import {
  Card,
  Input,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Button,
  Checkbox,
  Snackbar,
  Alert,
  CardActionArea,
} from "@mui/material";

import { Cancel } from "@mui/icons-material";

import {
  CreateSubCategory,
  GetCategoriesAndCount,
  GetLastSubCategoryPosition,
} from "../Assets/HttpBank";

const AddSubCategory = (prop) => {
  const navigate = useNavigate();
  const [Name, setName] = useState("");
  const [Category, setCategory] = useState({});
  const [Categories, setCategories] = useState([]);
  const [Position, setPosition] = useState(0);
  useEffect(() => {
    (async () => {
      const Data = await GetCategoriesAndCount();
      const Categories = Data?.Categories;
      setCategories([...Categories]);
      if (Categories[0]) {
        setCategory(Categories[0]);
      }
    })();
  }, []);

  useEffect(() => {
    (async () => {
      const { Position = 0 } = await GetLastSubCategoryPosition({ Category });
      setPosition(Position);
    })();
  }, [Category]);

  const Create = async () => {
    prop?.Message?.({ Message: "Creating Sub Category...", Type: "info" });
    const CreatedSeries = await CreateSubCategory({
      Name,
      Category,
      Position,
    });

    if (!CreatedSeries?.Valid) {
      prop?.Message?.({
        Message: "Couldn't create the Sub Category.",
        Type: "error",
      });
      return;
    }

    prop?.Message?.({
      Message: `Sub Category created!`,
      Type: "success",
    });

    prop?.Refresh?.();
    prop?.Close?.();

    //Show snackbar with the message
  };

  return (
    <Card
      style={{
        position: "absolute",
        display: "flex",
        alignItems: "flex-start",
        justifyContent: "flex-start",
        flexDirection: "column",
        width: "30%",
        // height: "70%",
        zIndex: 2,
        padding: 20,
      }}
    >
      <div
        style={{
          width: "100%",
          display: "flex",
          flexDirection: "row",
          alignItems: "center",
          justifyContent: "space-between",
        }}
      >
        <h2 style={{ fontSize: 18, color: "grey", fontWeight: "bolder" }}>
          Add Sub Category.
        </h2>

        <CardActionArea
          onClick={() => {
            prop?.Close?.();
          }}
          style={{
            borderRadius: 20,
            width: 30,
            height: 30,
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <Cancel style={{ width: 20, height: 20, color: "black" }} />
        </CardActionArea>
      </div>
      <div style={{ width: "95%", height: 1, backgroundColor: "lightgrey" }} />

      <div
        style={{
          width: "100%",
          display: "flex",
          flexDirection: "row",
          alignItems: "center",
          justifyContent: "space-between",
          marginLeft: 10,
          marginTop: 30,
        }}
      >
        <Input
          type={"text"}
          value={Name}
          style={{
            width: "90%",
            height: 50,
            backgroundColor: "#e6e6e6",
            padding: 10,
            borderRadius: 10,
          }}
          disableUnderline={true}
          placeholder={"Category name"}
          onChange={(e) => {
            setName(e.target.value);
          }}
        />
      </div>

      <div
        style={{
          width: "100%",
          display: "flex",
          flexDirection: "row",
          alignItems: "center",
          justifyContent: "space-between",
          marginLeft: 10,
          marginTop: 30,
        }}
      >
        <Input
          type={"number"}
          value={Position}
          style={{
            width: "90%",
            height: 50,
            backgroundColor: "#e6e6e6",
            padding: 10,
            borderRadius: 10,
          }}
          disableUnderline={true}
          placeholder={"Position"}
          onChange={(e) => {
            setPosition(e.target.value);
          }}
        />
      </div>

      <FormControl
        style={{
          width: "90%",
          height: 60,
          //   backgroundColor: "lightgrey",
          padding: 10,
          marginTop: 30,
          borderRadius: 10,
        }}
      >
        <InputLabel id="demo-simple-select-label">Category?</InputLabel>
        <Select
          onChange={(e) => {
            setCategory(e.target.value);
          }}
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          value={Category}
          label="Age"
          // onChange={handleChange}
        >
          {Categories.map((Category) => {
            return <MenuItem value={Category}>{Category?.Name}</MenuItem>;
          })}
        </Select>
      </FormControl>

      <div style={{ width: "95%", height: 1, backgroundColor: "lightgrey" }} />

      <Button
        style={{
          width: "90%",
          marginLeft: 10,
          marginTop: 30,
          textAlign: "start",
          alignItems: "center",
          justifyContent: "flex-start",
          borderRadius: 10,
        }}
        onClick={() => {
          Create();
        }}
      >
        <h5>Create</h5>
      </Button>
    </Card>
  );
};

export default AddSubCategory;
